import {
  HeaderContainer,
  Logo,
  HeaderText,
  Left,
  Right,
  NavList,
  NavItem,
  HeaderButton,
  BurgerMenu,
  MenuIcon,
  SideMenu,
  CloseBtn,
  SideNavItem,
  SideNavButton
} from './Header.styled';
import logo from '../../images/logo.png';
import { useState } from 'react';

const Header = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
    console.log(isOpen);
  };

  return (
    <HeaderContainer>
      <Left>
        <BurgerMenu>
          <MenuIcon onClick={toggleMenu} />
        </BurgerMenu>
        <SideMenu className={isOpen ? 'open' : ''}>
          <CloseBtn onClick={toggleMenu} />
          <SideNavItem></SideNavItem>
          <SideNavItem>  </SideNavItem>
          <SideNavItem></SideNavItem>
          <SideNavItem></SideNavItem>
          <SideNavButton></SideNavButton>
        </SideMenu>
        <Logo src={logo} alt="Logo" />
        <HeaderText> </HeaderText>
      </Left>
      <Right>
        <NavList>
          <NavItem>  </NavItem>
          <NavItem></NavItem>
          <NavItem></NavItem>
        </NavList>
        <HeaderButton></HeaderButton>
      </Right>
    </HeaderContainer>
  );
};

export default Header;
